  display: flex;
  align-items: center;
  border-bottom: 3px solid var(--clr-dark);
}
.search_form:hover {
  border-bottom: 3px solid var(--clr-accent);
}
.search_form:focus-within {
  border-bottom: 3px solid var(--clr-accent);
